@import '../../styles/variables.scss';

.aside {
    width: 100%;
    max-width: 330px;
    min-width: 330px;   
    background-color: $color-basic;
    -webkit-box-shadow: 1px 0px 4px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 1px 0px 4px 0px rgba(0,0,0,0.2);
    box-shadow: 1px 0px 4px 0px rgba(0,0,0,0.2);
}

.aside__header {
    width: 100%;
}
.aside__header-setting{
    margin-top: 5px;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
}
.aside__header-setting-img{
    width: 32px;
    height: 32px;
    transition: .2s;
    cursor: pointer;
    &:hover{
        fill: $color-orange-hover
    }
}
.aside__header-setting-text{
    font-size: 28px;
    font-weight: 700;
}
.search{
    padding: 0px 10px;
    margin-top: 8px;
}
.search__input{
    border-radius: 30px;
    padding: 10px 15px 10px 15px;
    font-size: 16px;
    background-color: $bg-input-search;
    width: 100%;
    outline: 1px solid rgba(255, 0, 0, 0.5);
    transition: .2s;
    border: none;
    &:focus{
        padding-left: 12px;
        outline: 1px solid rgba(255, 0, 0, 1);
    }
}