@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';
.registrations .container-registrations-margin{
    margin-top: 20px;
}
.container-registrations{
    width: 100%;
    max-width: 500px;
    margin: 100px auto 0 auto;
    @include phone(){
        margin-top: 20px;
    }
}
.registrations{
    padding: 0px 10px;
}
.logo__text{
    padding-bottom: 10px;
}
.registrations__form{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, .1), 0px 8px 16px rgba(0, 0, 0, .1);
    border: $border;
}
.registrations__form-wrap{
    text-align: center;
    padding: 16px 25px;
    background-color: $color-basic;
    display: flex;
    flex-direction: column;
    gap: 13px;
}
.registrations__form-text{
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    padding: 8px 0px;
}
.avatar{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}
.avatarEditVrap{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}
.avatar__img-edit{
    width: 0px;
    height: 0px;
    position: absolute;
    right: 0px;
    left: 0px;
    bottom: 0px;
    margin: 0 auto;
    z-index: 10;
    transition: .5s;
}
.avatar__img-wrap{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70px;
    height: 70px;
    border: 1px dashed  red;
    border-radius: 50%;
    &:hover .avatar__img-edit{
        width: 35px;
        height: 35px;
        bottom: 15px;
    }
}
.avatar__img{
    border-radius: 50%;
    width: 60px;
    height: 60px;
}
.avatar__text{
    font-size: 24px;
}
.avatar__btn{
    margin-top: 10px;
}